.App {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(119deg,#f7d627,#ecac11);
    padding: 80px 0;
    overflow: auto;
    box-sizing: border-box;
}

.App__container {
    width: 1500px;
    box-sizing: border-box;
    max-width: 90%;
    margin: 0 auto;
    border-radius: 8px;
    background-color: #FFF;
    box-shadow: 0 8px 48px 0 rgba(0,0,0,.25);
    padding: 48px;
}

.App__header {
    margin-bottom: 48px;
}
