.QuestionEdit {
    border: 1px solid #999;
    padding: 20px;
}

.QuestionEdit__row {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}
.QuestionEdit__indented + .QuestionEdit__indented {
    margin-top: 10px;
}
.QuestionEdit__indented > .QuestionEdit__indented + .QuestionEdit__indented {
    margin-top: 5px;
}


.QuestionEdit__wideItem {
    flex: 1;
}
* + .QuestionEdit__wideItem,
.QuestionEdit__wideItem + * {
    margin-left: 20px;
}

.QuestionEdit__submit {
    margin-right: 20px;
}

.QuestionEdit__toQuestion {
    padding-left: 50px;
}

.QuestionEdit__error {
font-size: 120%;
    color: red;
}

.QuestionEdit__bigEditor {
    height: 3em;
}

.QuestionEdit__gotoQuestionField {
    width: 100%;
}

.QuestionEdit__checkboxContainer {
    line-height: 1;
}
.QuestionEdit__checkbox {
    vertical-align: top;
}
